export default {
	data() {
		return {
			merchantList: [],
			searchID: "",
			searchname: "",
			merchant: {}
		}
	},
	onLoad() {
		this.__init()
	},
	methods: {
		
		toshopEarn(item) {
			console.log('item', item);
			uni.setStorageSync('merchant', item);
			uni.navigateTo({
				url: `/pages/shops-earn/shops-earn?id=${item.merchant_id}`,
			})
		},
		async __init() {
			this.$H.post("/agent/", {
				user_id: "100003",
				token: "dXQyMDIwMDMyMDE1MzkyODg5NDUxODA1",
				opt: "merchant_list",
				key_value: (this.searchID || this.searchname)
			}).then((data) => {
				console.log('接口调用了一次');
				this.merchantList = data.arr
				uni.setStorageSync('list', data)
			}).catch(() => {
				console.log("catch error!!");
			})
		},
		searchbtn() {
			this.__init()
		}
	}
}
